const $siteList = $('.siteList')
const $lastLi = $siteList.find('li.last')
const $input = $('input');
let focused = false
$input.on('focus', () => {
    focused = true
})
$input.on('blur', () => {
    focused = false
})
let sites = localStorage.getItem('sites')
sites = JSON.parse(sites)
sites = sites || [
    {logo: 'B', url: 'https://www.bilibili.com'},
    {logo: 'C', url: 'https://cssgradient.io'},
    {logo: 'G', url: 'https://github.com'},
    {logo: 'I', url: 'https://www.iconfont.cn/'},
    {logo: 'M', url: 'https://developer.mozilla.org/zh-CN/'}
]

const simplifyUrl = (url) => {
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace(/\/.*/, '') // 删除 / 开头的内容
}

const render = () => {
    $siteList.find('li:not(.last)').remove()
    console.log(sites);
    sites.forEach((node, index) => {
        const $li = $(`<li>
      <div class="site">
        <div class="logo">${node.logo}</div>
        <div class="link">${simplifyUrl(node.url)}</div>
        <div class="close">
          <svg class="icon">
            <use xlink:href="#icon-close"></use>
          </svg>
        </div>
      </div>
    </li>`).insertBefore($lastLi)
        $li.on('click', () => {
            window.open(node.url)
        })
        $li.on('click', '.close', (e) => {
            e.stopPropagation() // 阻止冒泡
            sites.splice(index, 1)
            render()
        })
    })
}
render()

$('.addButton').on('click', () => {
    let url = window.prompt('请问你要添加的网址是啥？')
    if (url.indexOf('http') !== 0) {
        url = 'https://' + url
    }
    console.log(url)
    sites.push({
        logo: simplifyUrl(url)[0].toUpperCase(),
        url: url
    })
    render()
})

window.onbeforeunload = () => {
    const string = JSON.stringify(sites)
    localStorage.setItem('sites', string)
}
const byKey = (e) => {
    if (focused) {
        return
    }
    const {key} = e
    for (let i = 0; i < sites.length; i++) {
        if (sites[i].logo.toLowerCase() === key) {
            window.open(sites[i].url)
        }
    }
}
$(document).on('keypress', byKey)